body {
  font-family: Arial, sans-serif;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background-color: #f0f0f0;
}

.header {
  width: 100%;
  background-color: #000;
  color: #fff;
  padding: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 16px;
  padding: 16px;
}

.product-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.product-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.product-info {
  padding: 16px;
}

.product-name {
  font-size: 18px;
  font-weight: bold;
}

.product-description {
  color: #555;
  margin: 8px 0;
}

.product-price {
  color: black;
  font-weight: bold;
}

.add-to-cart-button {
  background-color: black;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.add-to-cart-button:hover {
  background-color: #333;
}

.cart {
  width: 100%;
  background-color: #f8f8f8;
  padding: 16px;
  border-top: 1px solid #ddd;
}

.cart-title {
  font-size: 24px;
  margin-bottom: 16px;
}

.cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.cart-item-image {
  width: 60px;
  height: auto;
  margin-right: 16px;
}

.cart-item-info {
  flex: 1;
}

.cart-item-name {
  font-size: 16px;
  font-weight: bold;
}

.cart-item-price {
  color: black;
  font-weight: bold;
}

.empty-cart {
  text-align: center;
  color: #999;
}
